
*{
    font-family:'roboto', sans-serif;
    font-size: 18px;
}
body{
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(135, 135, 138);
}

.container{
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    margin: 0 auto;
    width: 256px;
    text-align: center;
    border: 10px solid rgb(14, 13, 13);
    background-color: rgb(14, 13, 13);
    border-radius: 5px;
}
.keypad{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(60px, auto);
}
input[type="text"]{
    height: 75px;
    width: 249px;
    background-color: rgb(14, 13, 13);
    border: 0px;
    color: antiquewhite;
    text-align: right;
    font-size: 25PX;
    font-weight: 500;
    letter-spacing: 1PX;

}
#clear{
    grid-column: 1/3;
    grid-row: 1;
    color: #262834;
}
#result{
    grid-column: 3/5;
    grid-row: 5;
    color: #262834;
}
#backspace{
    color: #262834;

}

button{
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background: #262834;
    color: antiquewhite;
    font-weight: 500;

}
input:focus,
input:active,
button:focus,
button:active {
    outline: none;
}

.highlight{
    background-color: aqua;
}